.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Background overlay */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.modal-overlay .modal-content {
  background-color: #fff;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Subtle shadow for pop-up effect */
  width: 100%;
  max-width: 400px;
  text-align: center;
  position: relative;
  height: 50vh;
}

.modal-overlay h2 {
  margin-bottom: 20px;
  font-size: 1.5rem;
  color: #333;
}

.modal-overlay label {
  display: block;
  margin-top: 15px;
  font-size: 0.9rem;
  color: #555;
  text-align: left;
}

.modal-overlay input {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
}

.modal-overlay button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.modal-overlay button:disabled {
  background-color: #ccc;
}

.modal-overlay button:hover:not(:disabled) {
  background-color: #0056b3;
}

.modal-overlay .close-button {
  position: absolute;
  top: 10px;
  right: 15px;
  background: none;
  border: none;
  font-size: 1.2rem;
  cursor: pointer;
}

.modal-overlay .close-button:hover {
  color: #f44336;
}

/* Resend OTP text styling */
.resend-otp-text {
  margin-top: 10px;
  font-size: 0.85rem;
  color: #007bff;
  cursor: pointer;
}

.resend-otp-text:hover {
  text-decoration: underline;
}
