/* Basic styles for the admin panel */
.admin-panel {
  width: 90%;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
  text-align: center;
}

/* Styling the buttons for tabs */
.tab-buttons button {
  margin: 10px;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

.tab-buttons button:hover {
  background-color: #0056b3;
}

/* Pending users table styling */
.pending-table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
  max-height: 40vh;
  overflow-y: auto;
}

.pending-table th,
.pending-table td {
  border: 1px solid #ddd;
  padding: 8px;
}

.pending-table th {
  background-color: #f2f2f2;
  text-align: left;
}

.pending-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.pending-table tr:hover {
  background-color: #ddd;
}

/* Pending users list buttons */
.auth-button,
.decline-button {
  margin-left: 10px;
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.auth-button {
  background-color: #28a745;
  color: white;
}

.auth-button:hover {
  background-color: #218838;
}

.decline-button {
  background-color: #dc3545;
  color: white;
}

.decline-button:hover {
  background-color: #c82333;
}

/* Modal styling */
.modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  /* overflow: auto; */
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  background-color: white;
  margin: 2% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 90%;
  height: 80vh;
  overflow: auto;
  text-align: left;
  border-radius: 10px;
}

.close-button {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
}

.close-button:hover,
.close-button:focus {
  color: black;
}

.modal-content ul {
  list-style-type: none;
  padding: 0;
}

.modal-content li {
  padding: 5px 0;
}

/* Mobile Responsive Styles */
@media screen and (max-width: 768px) {
  /* The table headers should still be visible */
  .pending-table th {
    background-color: #f2f2f2;
    font-weight: bold;
    text-align: left;
  }

  /* Stack each cell under the header on mobile */
  .pending-table,
  .pending-table thead,
  .pending-table tbody,
  .pending-table th,
  .pending-table td,
  .pending-table tr {
    display: block;
  }

  .pending-table tr {
    margin-bottom: 10px;
    border-bottom: 2px solid #ddd;
  }

  /* Display headers and cells as inline blocks */
  .pending-table td {
    position: relative;
    text-align: left;
    padding-left: 5%;
    display: block;
  }

  /* Display the headers as labels for each cell */
  .pending-table td:before {
    content: attr(data-label);
    position: absolute;
    left: 10px;
    top: 50%;
    transform: translateY(-50%);
    font-weight: bold;
    text-align: left;
  }
}
