/* Container for the login component */
.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  border-radius: 10px;
  background-color: #f4f4f4;
  font-family: "Arial", sans-serif;
}

/* Title Styling */
.login-title {
  margin-bottom: 20px;
  font-size: 24px;
  color: #333;
}

/* Input field container */
.input-container {
  margin: 10px 0;
}

/* Styling for the input fields */
.login-input {
  padding: 10px;
  font-size: 16px;
  width: 250px;
  border: 1px solid #ccc;
  border-radius: 4px;
  outline: none;
  transition: border-color 0.3s ease-in-out;
}

.login-input:focus {
  border-color: #007bff;
}

/* Button container to hold both buttons */
.button-container {
  margin-top: 20px;
  display: flex;
  justify-content: space-around;
  width: 100%;
  max-width: 300px;
}

/* Styling for the login and signup buttons */
.login-button,
.signup-button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  outline: none;
  transition: background-color 0.3s ease;
}

/* Styling for Login button */
.login-button {
  background-color: #007bff;
  color: white;
}

.login-button:hover {
  background-color: #0056b3;
}

/* Styling for Signup button */
.signup-button {
  background-color: #28a745;
  color: white;
}

.signup-button:hover {
  background-color: #218838;
}
